import "./App.css";

// Firebase SDK
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";

// Firebase Hooks
import { useAuthState } from "react-firebase-hooks/auth";
import { useCollectionData } from "react-firebase-hooks/firestore";
import { useRef, useState, useEffect } from "react";
import Scrollbar from "react-scrollbars-custom";

firebase.initializeApp({
  apiKey: "AIzaSyA_Gvjs7olhV7ibEqA2hhJrqHY6sEPrsCw",
  authDomain: "chat-app-6e13f.firebaseapp.com",
  projectId: "chat-app-6e13f",
  storageBucket: "chat-app-6e13f.appspot.com",
  messagingSenderId: "1093211163901",
  appId: "1:1093211163901:web:be1903b26bbed4d4094294",
  measurementId: "G-8PQQ1V5X74",
});

const auth = firebase.auth();
const fireStore = firebase.firestore();

// My Custom Components //

// SignIn Component
function SignIn() {
  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  };
  return (
    <div className="sign-in">
      <div className="welcome">Welcome To Our Chat</div>
      <button onClick={signInWithGoogle}>Sign In with Google</button>
    </div>
  );
}

// SignOut Component
function SignOut() {
  return (
    auth.currentUser && (
      <button
        onClick={() => {
          auth.signOut();
        }}
      >
        SignOut
      </button>
    )
  );
}

// ChatMessage Component
function ChatMessage(props) {
  const { text, uid, photoURL } = props.message;
  const messageClass = uid === auth.currentUser.uid ? "sent" : "recieved";
  return (
    <div className={`message ${messageClass}`}>
      <img src={`${photoURL}`} alt="Pic" />
      <span className="text"> {text}</span>
      <hr />
    </div>
  );
}

// ChatRoom Component
function ChatRoom() {
  const messageRef = fireStore.collection("messages");
  // const query = messageRef.orderBy("createdAt").limit(100);
  const query = messageRef.orderBy("createdAt");
  const [messages] = useCollectionData(query, { idField: "id" });
  const [formValue, setFormValue] = useState("");

  const messagesEndRef = useRef(null);
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const sendMessage = async (e) => {
    e.preventDefault();
    const { uid, photoURL } = auth.currentUser;

    if (formValue !== "") {
      setFormValue("");
      await messageRef.add({
        text: formValue,
        createdAt: firebase.firestore.FieldValue.serverTimestamp(),
        uid,
        photoURL,
      });
    }
  };

  return (
    <div className="chat-room">
      <div className="head">
        <span>{auth.currentUser.displayName}</span>
        <SignOut />
      </div>
      <Scrollbar id="messages-scroll">
        {messages && (
          <div className="messages-container">
            {messages.map((msg) => (
              <ChatMessage key={msg.id} message={msg} />
            ))}
            <div ref={messagesEndRef} />
          </div>
        )}
      </Scrollbar>
      <form className="input-form" onSubmit={sendMessage}>
        <input
          className="form-control"
          placeholder="Enter your message"
          value={formValue}
          onChange={(e) => {
            setFormValue(e.target.value);
          }}
        />
        <button type="submit">Send</button>
      </form>
    </div>
  );
}

function App() {
  const [user] = useAuthState(auth); //user is the first element in the return of 'useAuthState'
  console.log(user);

  return <div className="App">{user ? <ChatRoom /> : <SignIn />}</div>;
}

export default App;
